.layout {
  display: -webkit-flex;
  display: flex;
  height: 100%;
  width: 100%;
}

.layout__side {
  overflow: auto;
}

.layout__main {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

textarea {
  resize: none;
}

.map {
  height: 100%;
  width: 100%;
}

.geo-json {
  display: block;
  padding: 6px;
  white-space: nowrap;
}

.tile-radio {
  display: block;
  padding: 6px;
  white-space: nowrap;
}

.marker {
  transition: -webkit-transform 500ms ease-in-out;
  transition: transform 500ms ease-in-out;
  transition: transform 500ms ease-in-out, -webkit-transform 500ms ease-in-out;
}

.app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100%;
}

.app__nav {

}

.app__nav-link {
  display: inline-block;
  margin: 5px;
}

.app__content {
  display: -webkit-flex;
  display: flex;
  height: 100%;
}

fieldset {
  border: 1px solid lightgray;
}

html, body, #root {
  height: 100%;
  width: 100%;
}

body {
  background-color: #2b2b2b;
  color: #c4eefe;
  font-family: "Ubuntu Mono", monospace;
  font-size: 24px;
  -webkit-font-smoothing: antialiased;
  margin: 0;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
}

a {
  color: #56c6ff;
}

code {
  background-color: #3a3a3a;
  border: 2px solid #4d4d4d;
  color: #c4eefe;
  display: block;
  font-family: "Ubuntu Mono", monospace;
  padding: 0.5em 1em;
  white-space: pre;
}

fieldset {
  border: 2px solid #505050;
}

legend {
  color: #73c946;
}

input, textarea, select, button {
  background-color: #3a3a3a;
  border-color: #3c3c3c;
  color: #8c8c8c;
  font-family: "Ubuntu Mono", monospace;
  font-size: 24px;
  padding: 0.5em 1em;
  vertical-align: middle;
}

input[type="checkbox"], input[type="radio"] {
  height: 1em;
  width: 1em;
}

label {
  vertical-align: middle;
}

button {
  background-color: #4f595e;
  border: 2px solid #696f6f;
  border-radius: 4px;
  color: #c4eefe;
  font-weight: 500;
  padding: 0.5em 1em;
}

